#site-navigation {
  display: none;

  @media screen and (min-width: $screen-sm) {
    display: block;
  }
}

.site-header {

  .flex-container {
    justify-content: space-between;
    align-items: center;
  }

  .drawer-toggle {

    .opened {
      display: none;
    }

    &.open {

      .opened {
        display: block;
      }

      .closed {
        display: none;
      }
    }
  }

  .topbar {
    background-color: $color-primary;

    .flex-container {
      min-height: $topbar-mobile-height;
    }

    a,
    .drawer-toggle {
      color: rgba($color__background-body, 0.8);
      font-size: 0.888em;

      &:hover {
        color: rgba($color__background-body, 1);
      }
    }

    .icon {
      margin-right: 2px;
    }

    .drawer-toggle {
      padding: 15px 0;
    }
  }

  .drawer-wrapper {
    position: relative;
  }

  .drawer {
    display: none;
    background-color: $color-drawer-background;
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    padding: 5% 0;
    box-shadow: 0 6px 6px rgba(130, 144, 152, 0.25);
  }

  .drawer-open {
    animation-name: fadeIn;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    display: block;
  }

  .menu {

    ul {

      li {
        position: relative;
        border-bottom: 1px dotted rgba($color__background-body, 0.2);
        color: rgba($color__background-body, 0.8);

        .dropdown-toggle,
        a {
          line-height: 1.4em;
        }

        .dropdown-toggle {
          font-size: 1.11em;
          width: 50px;
          padding: 14px 0;
          position: absolute;
          top: 0;
          right: 0;
        }

        a {
          display: inline-block;
          padding: 15px 10px 15px 0;
          color: inherit;
        }
      }

      ul {
        padding-left: 15px;
        display: none;

        &.toggled-on {
          display: block;
        }
      }
    }
  }
}

.site-header .main-navigation ul li .dropdown-toggle {
    position: relative;
    display: inline-block;
    width: auto;
    padding: 0;
    line-height: 2em;
    padding-left: 5px;
}

.main-navigation {

  .menu {

    > ul {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: flex-end;

      > li {
        padding: 0 15px;

        > a {
          color: $color__text-main;
        }
      }
    }

    ul {

      li {
        border-bottom: 0;

        a {
          padding: 0;
          margin-bottom: 15px;
          line-height: 2em;
        }
      }
    }

    .page_item_has_children,
    .menu-item-has-children {

      &:hover {

        .children {
          display: block;
        }
      }
    }

  }
}

.site-identity {
  padding: 3% 0;
}

.site-branding {

  @media screen and (min-width: $screen-sm) {
    max-width: 40%;
  }

  @media screen and (min-width: $screen-md) {
    max-width: 30%;
  }
}

.site-title {
  font-size: 1.22em;
  line-height: 1em;
  /*line-height: 1.2em;*/
  margin-bottom: 0;
  margin-top: 0;

  a {
    color: $color-primary;
  }

  @media screen and (min-width: $screen-sm) {
    line-height: 1.2em;
  }
}

.site-description {
  font-size: 0.72em;
  font-style: italic;
  color: rgba($color-primary, 0.6);
  margin: 0;
  margin-top: 3px;
}
