h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: $font__main;
	font-weight: $font-weight-normal;
	line-height: 1.2em;
	margin-bottom: 3%;
}

h1 {
	font-size: 2.4em;

	@media screen and (min-width: $screen-sm) {
		font-size: 3.3em;
	}
}

h2 {
	font-size: 2.1em;

	@media screen and (min-width: $screen-sm) {
		font-size: 2.7em;
	}
}

h3 {
	font-size: 1.5em;

	@media screen and (min-width: $screen-sm) {
		font-size: 2.2em;
	}
}

h4 {
	font-size: 1.3em;

	@media screen and (min-width: $screen-sm) {
		font-size: 1.6em;
	}
}

h5 {
	font-size: 1.1em;
}

h6 {
	font-size: 0.8em;
}
