.menu {

	ul {
		padding: 0;
		margin: 0;
		list-style-type: none;

		a {
			text-decoration: none;
		}
	}

	.dropdown-toggle {
		border: none;
		border-radius: 0;
		box-shadow: none;
		display: block;
		background: none;
		color: inherit;
		cursor: pointer;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}
